.slideout-menu {
  position: fixed;
  left: 0;
  top: 0;
  bottom: 0;
  right: 0;
  z-index: 0;
  width: 276px;
  overflow-y: auto;
  -webkit-overflow-scrolling: touch;
  display: none;
  .header-column {
    width: 276px;
    padding-right: 15px;
    padding-left: 15px;
    padding-top: 27px;
    background: rgba(200, 200, 200, 1);
  }
  .row-line {
    margin: 0;
  }

  .toggle {
    margin: 0 5px 0 0;
  }

  .search-block {
    input[type="submit"] {
      padding-right: 35px;
      background: #fafafa url(../images/search.png) right center no-repeat;
      border-top-right-radius: 4px;
      border-bottom-right-radius: 4px;
      border: none;
      height: 35px;
    }

    .search-field {
      padding: 20px 0 0;
    }
  }

  p {
    color: #0068c8;
    font-size: 18px;
    font-weight: bold;
    padding: 10px 0px 10px 4px;
    margin-bottom: 0px;
  }

  ul {
    background: transparent;
    list-style-type: none;
    padding: 0 20px 20px 0;
    margin: 0 0 10px;
    font-size: 16px;
    border: 0;
    li {
      display: block;
      border-left: 2px solid transparent;
      padding: 0 0px 0 10px;
      a {
        color: #111;
        display: block;
        padding: 12px 0 10px;
        border-bottom: 1px solid #ddd;
      }
    }
  }
}

.slideout-panel {
  position:relative;
  z-index: 1;
  // will-change: transform;
}

.slideout-open,
.slideout-open body,
.slideout-open .slideout-panel {
  overflow: hidden;
}

.slideout-open .slideout-menu {
  display: block;
  overflow-x: hidden;
}

.col-sm-6.col-md-5.header-column:before,
.col-sm-6.col-md-5.header-column:after {
  content: '';
  display: block;
  clear: both;
}
.gtk_menu {
  background: rgba(200, 200, 200, 1);
}
.gtk_menu .header-column {
  width: 276px;
  padding-right: 0;
  padding-top: 27px;
  background: rgba(200, 200, 200, 1);
}

.gtk_menu .menu-column {
  left: 0;
  padding-right: 0;
  max-width: 100%;
  display: block;
  position: relative;
  top: 20px;
  left: -14px;
}
.gtk_menu .menu-column ul {
  background: transparent;
  border: 0;
}
@media (min-width: 512px) {
  .gtk_menu .logo {
    display: none;
  }
  .gtk_menu .toggle {
    display: none;
  }
}
@media (max-width: 767px) {
  .slideout-open .slideout-menu a.toggle {
    background-image: url(../images/close_menu.svg);
  }
}
@media (min-width: 768px) {
  .slideout-open .slideout-panel a.toggle {
    background-image: url(../images/close_menu.svg);
  }
}

.logo {
  img {
    @media (max-width: 991px) {
      width: 200px;
    }
  }
}

.menu {
  border-top: 1px solid #294886;
  text-align: center;
  margin: 10px 0 0;
  @media screen and (max-width: 667px) {
    display: none;
  }
  ul {
    display: inline-block;
    list-style: none;
    padding: 0;
    margin: 0;
    li {
      position: relative;
      list-style: none;
      display: inline-block;
      &:after {
        content: '';
        position: absolute;
        width: 1px;
        height: 20px;
        top: 20px;
        right: 0;
        background-color: #294886;
      }

      &:last-child {
        @media screen and (max-width: 930px) {
          display: none;
        }
        &:after {
          display: none;
        }
      }

      a {
        font-family: 'Roboto', Arial, sans-serif;
        font-weight: 700;
        font-size: 13px;
        color: #000;
        text-decoration: none;
        text-transform: uppercase;
        padding: 13px 24px;
        line-height: 60px;
        margin: 0 9px;
        @media screen and (max-width: 1187px) {
          padding: 13px 10px;
        }
      }
    }
  }

  .menu-cart-count {
    position: relative;
    margin-left: 9px;
    top: -1px;
    display: inline-block;
    width: 25px;
    height: 19px;
    line-height: 19px;
    color: #fff;
    background-color: #fe5656;
    text-align: center;
    font-family: robotomedium, Arial, sans-serif;
    font-size: 11px;
    &::before {
      content: '';
      position: absolute;
      background-color: #fe5656;
      left: -4px;
      top: 6px;
      width: 7px;
      height: 7px;
      transform: rotate(45deg);
    }
  }
}
