.sr-only {
  position: absolute;
  width: 1px;
  height: 1px;
  padding: 0;
  margin: -1px;
  overflow: hidden;
  clip: rect(0, 0, 0, 0);
  border: 0;
}

.sr-only-focusable:active,
.sr-only-focusable:focus {
  position: static;
  width: auto;
  height: auto;
  margin: 0;
  overflow: visible;
  clip: auto;
}

body {
  background: url(../images/bg.png) repeat;
  background-attachment: fixed;
}

html,
body {
  height: 100%;
}

.container {
  padding-right: 15px;
  padding-left: 15px;
  margin-right: auto;
  margin-left: auto;
}
@media (min-width: 768px) {
  .container {
    width: 750px;
  }
}
@media (min-width: 992px) {
  .container {
    width: 970px;
  }
}
@media (min-width: 1200px) {
  .container {
    width: 1170px;
  }
}

.row-line {
  margin-right: -15px;
  margin-left: -15px;
  display: flex;
  flex-wrap: wrap;
  align-items: flex-start;
}
.row-line.no-margin {
  margin: 0;
}
.row-line__between {
  justify-content: space-between;
}

@media (max-width: 991px) and (min-width: 768px) {
  .hidden-sm {
    display: none !important;
  }
}

@media (max-width: 767px) {
  .hidden-xs {
    display: none !important;
  }
}

@media (max-width: 1199px) and (min-width: 992px) {
  .hidden-md {
    display: none !important;
  }
}

@media (min-width: 1200px) {
  .hidden-lg {
    display: none !important;
  }
}

.title {
  font-size: 36px;
  font-weight: bold;
  background: url(../images/border.png) bottom left no-repeat;
  padding-bottom: 10px;
  margin: 0 0 20px;
}

h1, h2, h3, h4, h5, h6,
.h1, .h2, .h3, .h4, .h5, .h6 {
  font-family: inherit;
  font-weight: 500;
  line-height: 1.1;
  color: inherit;
}

h2, h3 {
  font-size: 22px;
  font-weight: bold;
}

h2, h3, h4, h5, h6 {
  color: #000;
  margin: 0 0 10px;
  padding: 10px 0;
}

h4, .h4 {
  font-size: 18px;
}

a {
  text-decoration: none;
}
a:hover {
  text-decoration: underline;
}

.wrapper {
  background: url(../images/bg.png) repeat;
  background-attachment: fixed;
  overflow: hidden;
  transition: transform 0.5s, -webkit-transform 0.5s;
}

root {
  display: block;
}

input, button, select, textarea {
  font-family: inherit;
  font-size: inherit;
  line-height: inherit;
}

.top-bar {
  background: #cfcfcf;
  padding: 5px 10px;
  max-width: 1200px;
  overflow: visible;
  min-height: 27px;
  margin: 0 auto;
  position: fixed;
  left: 50%;
  transform: translateX(-50%);
  z-index: 223;
  top: 0;
  width: 100%;
}
.top-bar .column-back {
  padding: 0 15px;
}
@media (min-width: 768px) {
  .top-bar .column-back {
    width: 16.66666667%;
  }
}
.top-bar .column {
  padding: 0 15px;
}
@media (min-width: 768px) {
  .top-bar .column {
    width: 83.33333333%;
  }
}
.top-bar .column-left {
  padding: 2px 100px 0 0;
}
@media (min-width: 992px) {
  .top-bar .column-left {
    width: 75%;
  }
}
.top-bar .column-right {
  padding: 0 15px;
}
@media (min-width: 992px) {
  .top-bar .column-right {
    width: 25%;
  }
}
.top-bar__enter, .top-bar__link {
  font-family: Roboto, sans-serif;
  color: #294886;
  font-size: 12px;
  text-decoration: none;
}
.top-bar__cart {
  color: #294886;
  padding: 0 0 0 25px;
  background: url(../images/header_top_cart.svg) no-repeat left 50%;
  text-decoration: none;
  font-size: 12px;
  font-family: Roboto, sans-serif;
}
.top-bar__count {
  background: #ff5100;
  border-radius: 10px;
  display: inline-block;
  color: #294886;
  font-size: 10px;
  padding: 3px 4px;
  height: 16px;
  line-height: 11px;
  text-align: center;
  min-width: 17px;
}
.top-bar__welcome {
  padding: 0 0 0 6px;
  color: #294886;
  font-size: 12px;
  margin: 4px 0 0;
  display: inline-block;
}
.top-bar__back {
  font-family: Roboto, sans-serif;
  font-size: 12px;
  float: left;
  color: #294886;
  margin: -3px 0 0;
  text-align: right;
  text-decoration: none;
  display: inline-block;
}
.top-bar__back svg {
  margin: 3px 0 0;
  display: inline-block;
  float: left;
}

.header {
  padding: 10px 0 0;
  position: relative;
}
@media (max-width: 991px) {
  .header {
    height: auto;
    padding-bottom: 17px;
  }
}
.header.fixed-top-centered {
  position: relative;
  display: block;
  margin: 26px auto 0;
}
.header .header-column {
  padding-right: 15px;
  padding-left: 15px;
}
@media (min-width: 768px) {
  .header .header-column {
    width: 50%;
  }
}
@media (min-width: 992px) {
  .header .header-column {
    width: 41.66666667%;
  }
}
.header .header-search-block {
  padding: 28px 0 0;
}
@media (min-width: 992px) {
  .header .header-search-block {
    width: 33.33333333%;
  }
}
.header .contacts-column {
  padding-right: 15px;
  padding-left: 15px;
}
@media (min-width: 768px) {
  .header .contacts-column {
    width: 50%;
  }
}
@media (min-width: 992px) {
  .header .contacts-column {
    width: 25%;
  }
}
.header .logo {
  padding: 16px 22px 0 0;
  display: block;
  float: left;
}
@media (max-width: 400px) {
  .header .logo {
    padding-top: 8px !important;
  }
}
@media (max-width: 991px) {
  .header .logo img {
    width: 200px;
  }
}

.toggle {
  width: 34px;
  height: 17px;
  background: url(../images/toggle.svg) bottom left no-repeat;
  border: 0;
  float: left;
  display: block;
  padding-top: 53px;
  z-index: 2;
  position: relative;
}
@media (max-width: 400px) {
  .toggle {
    padding-top: 45px;
  }
}

.top-navigation {
  padding: 0 15px 15px;
}
@media (max-width: 400px) {
  .top-navigation {
    padding-bottom: 0;
  }
}
.top-navigation a {
  display: block;
  float: left;
  margin-left: 20px;
  text-decoration: none;
  font-size: 12px;
  color: #222;
}

.social-item {
  width: 26px;
  height: 26px;
  margin-left: 10px;
  background: url(../images/instagram.svg) no-repeat center center;
  border-radius: 5px;
}
@media (max-width: 767px) {
  .social-item {
    opacity: 0;
    pointer-events: none;
  }
}

.contacts-list {
  list-style: none;
  font-size: 14px;
  padding: 5px 0 0;
  margin: 0;
  text-align: right;
}
@media (max-width: 540px) {
